.contact-edit {
    h2 {
        margin: auto;
        margin-bottom: 1rem;
    }
    .contact-form {
        background-color: rgba(255, 255, 255, 0.219);
        width: 17.5rem;;
        margin: auto;
        padding-top: 1rem;
        border-radius: 5px;
        >* {
            margin-bottom: .5rem;
        }

        input {
            width: 15rem;
            background-color: transparent;
            border: none;
            color: #f6f6f6;
            border-bottom: 1px solid white;
            transition: .2s;
            &::placeholder {
                color: #f6f6f6
            }
            &:hover, &:focus {
                border-color: #ec436d;
                &::placeholder {
                    color: #ec436d
                }
            }
        }
        button {
            margin-top: .5rem;
            font-weight: bold;
            font-size: 1.25rem;
            padding: .4rem 1.3rem;
        }
    }
}