.contact-preview {
    position: relative;
    background-color: #f6f6f6dc;
    color: #0b0720;
    cursor: pointer;
    transition: .3s ease-in-out;
    box-shadow: 0 0 10px rgba(#fff, .46);
    padding: .5rem 0;
    border-radius: 5px;
    &:hover {
        transform: scale(1.025);
        button {
            display: block;
        }
    }
    img {
        height: 100px;
        margin-block-end: .5rem;
    }
    button {
        position: absolute;
        background-color: transparent;
        color: rgb(255, 68, 68);
        font-size: 1.15rem;
        right: 0;
        top: 0;
        display: none;
        &:hover {
            transform: rotate(35deg);
        }
    }
}