.transfer-funds {
    background-color: rgba(255, 255, 255, 0.144);
    width: 17.5rem;
    margin: 1rem auto;
    padding: 1rem 1.5rem;
    padding-bottom: 1.5rem;
    border-radius: 5px;
    h3 {
        font-size: 1.1rem;
        margin-bottom: .5rem;
    }
    p {
        margin-bottom: .5rem;
    }
    form {
        position: relative;
        input {
            width: 80%;
            height: 2.5rem;
            border: none;
            border-radius: 5px 0 0 5px;
        }

        small {
            color: red;
            position: absolute;
            bottom: -1.25rem;
            left: 0;
        }

        button {
            width: 20%;
            border-radius: 0 5px 5px 0;
            height: 2.5rem;
        }
    }
}