.login-page {
    h2 {
        margin: auto;
        margin-bottom: 1rem;
    }
    form {
        position: relative;
        background-color: rgba(255, 255, 255, 0.281);
        width: 17.5rem;
        padding: 1rem 0;
        margin: auto;
        border-radius: 5px;
        box-shadow: 0 0 8px rgba(#fff, .34);
        input {
            width: 15rem;
            margin-bottom: 1.5rem;
            font-size: 1rem;
            padding: 0.75rem 1rem;
            border-radius: 5px;
            border: 1px solid #a7a7a7;
            &:hover {
                box-shadow: 0 0 8px rgba(#fff, 0.52);
                border-color: complement(#e54e6d);
            }
        }
        small {
            color:rgba(255, 53, 53, 0.993);
            position: absolute;
            top: 4rem;
            left: 1.35rem;
        }
        button:first-of-type {
            font-size: 1.15rem;
            padding: .5rem 1.25rem;
        }
        button:last-of-type {
            background-color: transparent;
            margin-bottom: 0;
            padding-bottom: 0;
            &:hover {
                text-decoration: underline;
            }
        }
    }
}