.move-list {
    text-align: center;
    background-color: rgba(255, 255, 255, 0.151);
    width: 17.5rem;
    margin: auto;
    padding: .25rem 0;
    box-shadow: 0 0 8px rgba(#fff, .36);
    .header {
        padding-top: .5rem;
        font-size: 1.25rem;
    }
    .no-moves {
        padding: 1rem 0;
        font-size: 1.25rem;
    }
    .move {
        background-color: #f6f6f6;
        color: #202020;
        padding: .5rem 0;
        border-radius: 5px;
        margin: 1rem;
    }
}