.home-page{
    position: relative;
    &>* {
        margin-bottom: 1rem;
    }
    h1 {
        font-size: 3rem;
        margin-top: 3rem;
        @media (max-width: 600px) {
            font-size: 2rem;
        }
    }
    button {
        font-size: 1.5rem;
        padding: .75rem 1.5rem;
        @media (max-width: 600px) {
            font-size: 1rem;
            padding: .5rem 1rem;
        }
    }

    .user-details {
        position: absolute;
        top: -1rem;
        width: 100%;
        border-radius: 0;
        background-color: #111111;
        padding: 1rem 0;
        h3 { 
            font-size: 1.5rem;
            border-bottom: 1px solid #bbb;
            padding-bottom: .5rem;
            width: fit-content;
            margin-bottom: .5rem;

        }
        >* {
            margin: 0 1rem;
        }
        p {
            font-size: 1.25rem;
            @media (max-width: 600px) {
                font-size: 1rem;
                margin: 0 .5rem;
            }
            i {
                color: rgb(254, 196, 17);
                margin-inline-end: .5rem;
            }
        }

    }
}