.contact-filter {
    margin-bottom: 1rem;
    border: 1px solid #e54e6d;
    width: fit-content;
    border-radius: 5px;

    input, button {
        height: 2.5rem;
        border: none;
        background-color: #fff;
    }
    input {
        width: 10rem;
    }
    .clear-btn {
        border-radius: 5px 0 0 5px;
        color: #bbb;
    }
    .search-btn {
        border-radius: 0 5px 5px 0;
        color: #bbb;
    }
    &:hover {
        box-shadow: 0 0 8px rgba(#fff, 0.52);
        border-color: complement(#e54e6d);
        button {
            color: complement(#e54e6d);
        }
    }
}