.contact-details {
    text-align: center;
    h2 { 
        margin: auto;
        margin-bottom: 1rem;
    }
    .details {
        margin-bottom: 1rem;
        h3 {
            font-size: 1.75rem;
            color: #ec4e6d;
            text-transform: capitalize;
        }
        img {
            margin-bottom: 1rem;
            height: 200px;
        }
        .info {
            background-color: rgba(255, 255, 255, 0.226);
            padding: 0.5rem 2rem;
            padding-top: 1rem;
            border-radius: 100px;
            p {
                font-size: 1.1rem;
                @media (max-width: 600px) {
                    font-size: .875rem;
                }
                margin-bottom: 0.5rem;
            }
            i {
                margin-inline-end: 1rem;
            }
        }
    }
    .actions {
        button {
            font-size: 1.1rem;
            font-weight: bold;
            text-transform: capitalize;
            margin: 0 .5rem;
        }
    }
}