.app-header {
    height: 3.5rem;
    background-color: #1a1a1a;
    color: #f6f6f6;
    margin-block-end: 1rem;
    box-shadow: 0px 7px 10px #00000033;
    .logo {
        font-family: 'Pacifico', cursive;
        margin-inline-start: 1rem;
        color: #ec4e6d;
        font-weight: bold;
        font-size: 1.5rem;
        transition: .3s;
        &:hover {
            color: lighten(#ec4e6d, 5)
        }
    }
    .main-nav {
        button {
            background-color: transparent;
            font-size: 1rem;
        }
        @media (max-width: 600px) {
            position: absolute;
            display: flex;
            flex-direction: column;
            top: 3.5rem;
            background-color: #3b3b3b;
            width: 100%;
            z-index: 1;
            box-shadow: 0px 4px 10px #00000096;
            transform: translateY(-135%);
            transition: .3s ease-in-out;
        }
        &.drop-nav {
            transform: translateY(0);
        }
        >*:not(:last-child) {
            margin-inline-end: 2rem;
        }
        >* {
            display: inline-flex;
            height: 3.5rem;
            width: 5rem;
            font-weight: bold;
            text-transform: uppercase;
            justify-content: center;
            align-items: center;
            transition: .3s ease-in-out;
            &.active {
                color: #ec4e6d;
                box-shadow: inset 0 -2px #ec4e6d;
                @media (max-width: 600px) {
                    width: 100%;
                    box-shadow: none;
                }
            }
            @media (max-width: 600px) {
                width: 100%;
                &:not(:last-child) {
                    border-bottom: 1px solid rgb(146, 146, 146);
                }
            }
        }
    }
    .hamburger {
        position: relative;
        display: none;
        margin-inline-end: .5rem;
        @media (max-width: 600px) {
            display: inline-flex;
        }
        background-color: inherit;
        span {
            transition: .3s ease-in-out;
            height: .2rem;
            width: 2.25rem;
            margin-bottom: .33rem;
            border-radius: 20px;
            background-color: #ec4e6d;
        }
        span:last-of-type {
            margin-bottom: 0;
        }
        &.drop-nav {
            span {
                position: absolute;
                left: -26px;
            }
            span:nth-child(1) {
                transform: rotate(135deg);
            }
            span:nth-child(2) {
                opacity: 0;
            }
            span:nth-child(3) {
                transform: rotate(-135deg);
            }
        }
    }
}