.contact-page {
    position: relative;
    @media (max-width: 600px ) {
        h2 {
            margin: auto;
            margin-bottom: 1rem;
        }
        .contact-filter {
            margin: auto;
            margin-bottom: 1rem;
        }
    }
    .add-btn {
        position: fixed;
        bottom: 1rem;
        right: 1.25rem;
        background-color: #ec4e6d;
        width: 3.5rem;
        height: 3.5rem;
        font-size: 1.5rem;
        border-radius: 50%;
        box-shadow: 0 0 8px rgba(#fff, .36);
    }
}